:root {
    --main-bg-color: brown;
  }

@font-face {
    font-family: pixelizer;
    src: url(../fonts/Pixelizer.ttf);
   
}

@font-face {
    font-family: future;
    src: url(../fonts/future.otf);
   
}

html {
    font-size: 0.12vh;
    overscroll-behavior: none; 
}

body {
    background: rgb(220, 229, 255);
    font-family: future;
    font-size: 15rem;
   margin: 0;
   user-select: none;
   letter-spacing: 0rem;
   overscroll-behavior: none; 
} 



.svgAccurate {
    shape-rendering:auto;
    
    width: 40rem;
    margin-top: 1.6rem;
}

.logo {

    width:64.97rem;

}
.wheel {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    width: fit-content;
    height: 83rem;
    background: #000593;
    box-shadow:  
     0rem 4rem 4rem 0rem rgba(0, 0, 0, 0.25),
     8rem 4rem 12.2rem 0rem rgba(0, 0, 0, 0.74);
    border-radius: 3.5rem;
}


.wheel::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow:   inset -4rem -15rem 15.3rem 0rem rgba(0, 0, 0, 0.81),
    inset 0rem 2rem 10.7rem 0rem rgba(0, 0, 0, 0.85);
    pointer-events: none; 
    z-index: 1; 
  }

.hole {
    width: fit-content;
    height: fit-content;
    padding: 9rem 2.5rem;
    background: #000000;
    border-radius: 7rem;
    margin-bottom: 7rem;    
}
.frame {
    box-sizing: border-box;
    padding: 9.75rem;
    width: fit-content;
    height: fit-content;
    background: #DCE5FF;
    box-shadow:  3rem 3rem 10.8rem 0rem rgba(15, 28, 148, 0.5), /*повышение чисел в тени меняет границу с аркообразной на часть разделеныый арки, что в дополнении придает более сильную выпуклость */ 
                inset -4.5rem -4.5rem 3.7rem 0rem rgba(14, 42, 150, 0.77),
                inset 3rem 3rem 5.55rem 0rem rgb(255, 255, 255),
                inset -7.5rem -7.5rem 4.65rem 0rem rgb(255, 255, 255),
                inset 10.5rem 10.5rem 16.65rem 0rem rgba(17, 47, 167, 0.68);
}

.greenBtn {
    border-radius: 12rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-sizing: border-box;
    padding: 0px 15rem;
    background: #67FF6F;
    box-shadow: inset 4.5rem 4.5rem 8.25rem 0rem rgba(10, 113, 15, 1),
    inset 10.5rem 10.5rem 10.8rem 0rem rgb(255, 255, 255),
    inset -7.5rem -7.5rem 6.75rem 0rem rgba(10, 113, 15, 1) ;
    font-family: future;
    height: 78rem;
    text-shadow: 1rem 1rem 4.6rem  rgba(0,0,0,0.58);
}
