    display: flex;
    align-items: center;
}
.device {
    
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
    border-radius: 18rem;
    width: 60vw;
    height: 67vh;
    background: #DCE5FF;
    box-shadow: inset 7rem 7rem 5.1rem 0rem rgb(255, 255, 255),
    inset -7rem -7rem 16.2rem 0rem rgba(15, 28, 148, 0.92),
    inset -20rem -20rem 8.3rem 0rem rgba(255, 255, 255, 0.92),
     -8rem -8rem 29rem 0rem rgb(255, 255, 255),
     5rem 10rem 26.4rem 0rem rgba(17, 47, 167, 0.71);

    padding: 18rem;
    
   
   
}

.device-display {
    width: 49%;
