@media (max-width:1237px) {
    .StartSection-title {
        font-size: 12rem;

    }
}

@media (max-width:930px) {
    .StartSection-title {
        font-size: 10rem;
    }
}


@media (max-width:879px) {
   .device {
    flex-direction: column;
    width: 70vw;
    height: 90vh;
   }
   .device-display {
   
    height: 49%;
    width: 100%;
   }
   .device-answers {
    margin-top: 20rem;
    height: 49%;
    width: 100%;
   }

   .buttonFrame {
    height: 90%;
}

.greenBtn {
    height: 48rem;
}
}



@media (max-width: 568px) {
    .device {
        box-sizing: border-box;
        box-shadow: none;
        width: 100%;
        height: 100%;
        margin: 0px;
    }
    .App {
        align-items:start;
    }

    .buttonFrame {
        height: 90%;
    }

    .greenBtn {
        height: 50rem;
    }
